import { z } from 'zod';

import {
  DatasetsInterface,
  DatasetsSchema,
  SemanticTriggersInterface,
  SemanticTriggersSchema,
  ConnectorsInterface,
  ConnectorsSchema,
} from './index';

export const TemplateSchema = z.object({
  identifier: z.string().min(1),
  version: z.string().min(1),
  name: z.string().min(1),
  prefix: z.string().min(1),
  description: z.string().min(1),
  datasets: DatasetsSchema,
  semanticTriggers: SemanticTriggersSchema,
  connectors: ConnectorsSchema.optional(),
});

export interface TemplateInterface {
  id: string;
  identifier: string;
  version: string;
  name: string;
  prefix: string;
  description: string;
  datasets: DatasetsInterface;
  semanticTriggers: SemanticTriggersInterface;
  connectors?: ConnectorsInterface;
}

export interface PostTemplateInterface extends Omit<TemplateInterface, 'id'> {
  identifier: string;
  version: string;
  name: string;
  prefix: string;
  description: string;
  datasets: DatasetsInterface;
  semanticTriggers: SemanticTriggersInterface;
  connectors?: ConnectorsInterface;
}

export interface TemplateVersionInterface {
  version: string;
  createdAt: Date;
  count: number;
}

export interface TemplateDetailInterface extends TemplateInterface {
  versions: Array<TemplateVersionInterface>;
}
